.details {
  &__wrapper {
    display: grid;
    row-gap: 40px;

    @include onTablet {
      row-gap: 48px;
    }
  }

  &__button-wrapper {
    @include pageGrid;
  }

  &__button {
    grid-column: 1/-1;

    @include onTablet {
      grid-column: 2 / -2;
    }

    @include onDesktop {
      grid-column: 5 / -5;
    }
  }
}

.detail {
  &__photos {
    @include pageGrid;
    margin-bottom: 16px;
  }

  &__link {

    &--wide {
      @include onTablet {
        grid-column: span 4;
      }

      @include onDesktop {
        grid-column: span 8;
      }
    }
    &--square {
      @include onTablet {
        grid-column: span 2;
      }
      @include onDesktop {
        grid-column: span 4;
      }
    }
    @include hover(transform, scale(1.05));
  }

  &__photo {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__content {
    @include pageGrid;
  }

  &__title {
    grid-column: 1/-1;
    margin-bottom: 8px;
    font-weight: 700;
    font-size: 24px;
    line-height: 140%;
  }

  &__description {
    grid-column: 1/-1;

    @include onDesktop {
      grid-column: span 8;
    }

    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
  }
}
