.page {
  font-family: "Poppins", sans-serif;
  color: $white-color;
  background-color: $black-color;
  font-size: 16px;
  line-height: 140%;
  scroll-behavior: smooth;
  overflow-x: hidden;

  &:has(.page__menu:target) {
    overflow: hidden;
  }

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transition: all 0.5s;
    opacity: 0;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}
