.icon {
  display: block;
  width: $icon-size-s;
  height: $icon-size-s;
  background-size: cover;
  transition: transform $durationEffect;

  @include hover(transform, scale(1.2));

  @include onTablet {
    height: $icon-size-m;
    width: $icon-size-m;
  }

  &--phone {
    background-image: url(../icons/icon-phone-call.svg);

    @media (min-width: 768px) {
      &:hover::after {
        position: absolute;
        width: max-content;
        content: "+1 234 555-55-55";
        font-size: 12px;
        line-height: 16px;
        letter-spacing: 2px;
        color: $white-color;
        transform: translate(-100%, 50%);
      }
    }
  }

  &--menu {
    background-image: url(../icons/icon-burger-menu.svg);
  }

  &--close {
    background-image: url(../icons/icon-close.svg);
  }
}
